import { apiHelper } from '../utils/helpers'

export default {
    categories: {
        get() {
            return apiHelper.get('/admin/categories')
        },
        create({ newCategoryData }) {
            return apiHelper.post('/admin/categories', newCategoryData, {
                headers: { 'Content-Type': 'application/json;charset=UTF-8' }
            }) 
        },
        delete({ categoryId }) {
            return apiHelper.delete(`/admin/categories/${categoryId}`)
        },
        update({ updatedCategoryData, categoryId }) {
            return apiHelper.put(`/admin/categories/${categoryId}`, updatedCategoryData, {
                headers: { 'Content-Type': 'application/json;charset=UTF-8' }
            })
        }
    },
    restaurants: {
        create({ formData }) {
            return apiHelper.post('/admin/restaurants', formData)
        },
        get() {
            return apiHelper.get('/admin/restaurants')
        },
        delete({ restaurantId }) {
            return apiHelper.delete(`/admin/restaurants/${restaurantId}`)
        },
        getDetail({ restaurantId }) {
            return apiHelper.get(`/admin/restaurants/${restaurantId}`)
        },
        update({ restaurantId, formData }) {
            return apiHelper.put(`/admin/restaurants/${restaurantId}`, formData)
        }
    },
    users: {
        get() {
            return apiHelper.get('/admin/users')
        },
        toggleRole({ userId, payLoad }) {
            return apiHelper.put(`/admin/users/${userId}`, payLoad, {
                headers: { 'Content-Type': 'application/json;charset=UTF-8' }
            })
        }
    }
}