<template>
  <div class="container py-5">
    <AdminNav />

    <form class="my-4" @submit.stop.prevent="createCategory">
      <div class="form-row">
        <div class="col-auto">
          <input
            type="text"
            class="form-control"
            placeholder="新增餐廳類別..."
            v-model="newCategoryName"
          >
        </div>
        <div class="col-auto">
          <button
            type="button submit"
            class="btn btn-primary"
            :disabled="isProcessing"
          >
            新增
          </button>
        </div>
      </div>
    </form>
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th
            scope="col"
            width="60"
          >
            #
          </th>
          <th scope="col">
            Category Name
          </th>
          <th
            scope="col"
            width="210"
          >
            Action
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
        v-for="category in categories"
        :key="category.id"
        >
        <th scope="row">
        {{ category.id }}
        </th>
            <td class="position-relative">
            <div
                v-show="!category.isEditing"
                class="category-name"
            >
                {{ category.name }}
            </div>
            <input
                v-show="category.isEditing"
                v-model="category.name"
                type="text"
                class="form-control"
            >
            <span
                v-show="category.isEditing"
                class="cancel"
                @click.stop.prevent="handleCancel(category.id)"
            >
                ✕
            </span>
            </td>
            <td class="d-flex justify-content-between">
            <button
                v-show="!category.isEditing"
                type="button"
                class="btn btn-link mr-2"
                @click.stop.prevent="toggleIsEditing(category.id)"
            >
                Edit
            </button>
            <button
                v-show="category.isEditing"
                type="button"
                class="btn btn-link mr-2"
                @click.stop.prevent="updateCategory({ categoryId: category.id, name: category.name })"
            >
                Save
            </button>
            <button
                type="button"
                class="btn btn-link mr-2"
                @click.stop.prevent="deleteCategory(category.id)"
            >
                Delete
            </button>
            </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style scoped>
.category-name {
  padding: 0.375rem 0.75rem;
  border: 1px solid transparent;
  outline: 0;
  cursor: auto;
}

.btn-link {
  width: 62px;
}

.cancel {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border: 1px solid #aaaaaa;
  border-radius: 50%;
  user-select: none;
  cursor: pointer;
  font-size: 12px;
}
</style>

<script>
import AdminNav from '@/components/AdminNav'
//import { v4 as uuidv4 } from 'uuid'
import adminAPI from '../apis/admin'
import { Toast } from '../utils/helpers'

export default {
  components: {
    AdminNav,
  },
  data () {
    return {
      categories: [],
      newCategoryName: '',
      isProcessing: false
    }
  },
  created () {
    this.fetchCategories()
  },
  methods: {
    async fetchCategories () {
      try {
        const { data } = await adminAPI.categories.get()
        this.categories = data.categories.map(category => ({
          ...category,
          isEditing: false,
          nameCached: '',
        }))
      } catch(err) {
        Toast.fire({
          icon: 'error',
          title: '無法取得類別資料，請稍後再試'
        })
        console.log('error', err)
      }
    },
    async createCategory() {
      try {
        if(!this.newCategoryName) {
          Toast.fire({
            icon: 'warning',
            title: '請輸入類別名稱'
          })
          return
        }

        this.isProcessing = true
        const newCategoryData = JSON.stringify({
          name: this.newCategoryName
        })
        const { data } = await adminAPI.categories.create({ newCategoryData })

        if(data.status !== 'success') {
          throw new Error(data.message)
        }

        Toast.fire({
          icon: 'success',
          title: '新增類別成功'
        })
        this.newCategoryName = ''
        this.isProcessing = false
        this.fetchCategories()
      } catch(err) {
        Toast.fire({
          icon: 'error',
          title: '無法新增類別，請稍後再試'
        })
        this.isProcessing = false
      }
    },
    async deleteCategory(categoryId) {
      try {
        const { data } = await adminAPI.categories.delete({ categoryId })
        if(data.status !== 'success') {
          throw new Error(data.message)
        }
        this.categories = this.categories.filter(c => c.id !== categoryId)
        Toast.fire({
          icon: 'success',
          title: '類別刪除成功'
        })
      } catch(err) {
          Toast.fire({
            icon: 'error',
            title: '無法刪除類別，請稍後再試'
          })
      }
    },
    toggleIsEditing(categoryId) {
        this.categories = this.categories.map(category => {
            if(category.id === categoryId) {
                return {
                    ...category,
                    isEditing: !category.isEditing,
                    nameCached: category.name
                }
            }
            return category
        })
    },
    async updateCategory({ categoryId, name }) {
      try {
        const updatedCategoryData = JSON.stringify({ name })
        const { data } = await adminAPI.categories.update({ updatedCategoryData, categoryId })
        if(data.status !== 'success') {
          throw new Error(data.message)
        }
        Toast.fire({
          icon: 'success',
          title: '更新類別成功'
        })
        this.toggleIsEditing(categoryId)
      } catch(err) {
        Toast.fire({
          icon: 'error',
          title: '無法更新類別資料，請稍後再試'
        })
        console.log('error', err)
      }
        

    },
    handleCancel(categoryId) {
        this.categories = this.categories.map(category => {
            if (category.id === categoryId) {
                return {
                    ...category,
                    name: category.nameCached
                }
            }
            return category
        })
        this.toggleIsEditing(categoryId)
    }
  }
}
</script>